/* Reduce icon size aggressively */
.social i,
.contact-icons i,
.contact-icons a i {
 font-size: 0.75rem !important; /* adjust: try 0.7rem, 0.65rem, etc. */
}

/* Reduce spacing between icons */
.social li,
.contact-icons li {
 margin: 0 0.2rem !important;
 padding: 0 !important;
}

/* Optional: scale the whole block (strongest fallback) */
.social .contact-icons {
 transform: scale(0.8);
 transform-origin: top left;
}
